import React from 'react'
import {
  makeStyles,
  Paper,
  Box,
  Typography,
  Grid,
  useTheme,
  useMediaQuery,
} from '@material-ui/core'
import { shape, number, string } from 'prop-types'
import cx from 'classnames'
import Bar from './Bar'
import debtSrc from '../assets/img/debt.svg'
import interestSrc from '../assets/img/interest.svg'
import accruedSrc from '../assets/img/accrued.svg'
import AnimatedNumber from './AnimatedNumber'
import { sizes } from '../constants/theme'

const useStyles = makeStyles((theme) => ({
  '@keyframes collapseHorizontally': {
    '0%': {
      width: 0,
    },
    '100%': {
      width: '100%',
    },
  },
  '@keyframes fadeIn': {
    '0%': {
      opacity: 0,
    },
    '100%': {
      opacity: 1,
    },
  },
  results: {
    background: theme.palette.background.secondary,
    maxWidth: 630,
    padding: `${theme.spacing(9)}px 0`,
    '&>div': {
      overflow: 'hidden',
    },
    '&:before': {
      content: '""',
      width: '100vw',
      background: theme.palette.background.secondary,
      height: 'calc(100% + 70px)',
      position: 'absolute',
      left: -10,
      bottom: 0,
      borderRadius: 10,
    },
    [theme.breakpoints.down('md')]: {
      animation: '$fadeIn 300ms ease-in-out',
    },
    [theme.breakpoints.up('md')]: {
      animation: '$collapseHorizontally 300ms ease-in-out',
      padding: 0,
      height: 541,
      '&:before': {
        width: 160,
        left: -146,
        height: '100%',
      },
    },
  },
  endResult: {
    marginTop: theme.spacing(2),
    fontSize: 26,
    fontWeight: 'bold',
    lineHeight: 1,
  },
  info: {
    fontSize: 20,
    fontWeight: 'bold',
    lineHeight: 1,
    marginTop: 2,
  },
  debt: {
    lineHeight: 1.2,
    '&:before': {
      background: `url(${debtSrc})`,
      content: '""',
      width: 10,
      height: 10,
      position: 'absolute',
      left: -20,
      top: 0,
      bottom: 0,
      margin: 'auto',
    },
    [theme.breakpoints.up('md')]: {
      '&:before': {
        content: 'initial',
      },
    },
  },
  interest: {
    '&:before': {
      background: `url(${interestSrc})`,
      content: '""',
      width: 10,
      height: 10,
      position: 'absolute',
      left: -20,
      top: 0,
      bottom: 0,
      margin: 'auto',
    },
    [theme.breakpoints.up('md')]: {
      '&:before': {
        content: 'initial',
      },
    },
  },
  accrued: {
    '&:before': {
      background: `url(${accruedSrc})`,
      content: '""',
      width: 10,
      height: 10,
      position: 'absolute',
      left: -20,
      top: 0,
      bottom: 0,
      margin: 'auto',
    },
  },
  container: {
    '& > *': {
      position: 'relative',
    },
    [theme.breakpoints.up('md')]: {
      margin: '64px 0 auto',
      width: 630,
    },
  },
  details: {
    marginLeft: theme.spacing(8),
    [theme.breakpoints.up('md')]: {
      marginLeft: theme.spacing(4),
    },
  },
  withContainer: {
    height: 390,
    [theme.breakpoints.up('md')]: {
      height: 325,
      order: 2,
    },
  },
  chartSection: {
    position: 'relative',
    display: 'flex',
    alignItems: 'center',
  },
  with: {
    padding: `${theme.spacing(4)}px 0 ${theme.spacing(6)}px ${theme.spacing(
      8
    )}px`,
    marginRight: theme.spacing(6),
    [theme.breakpoints.up('md')]: {
      paddingBottom: theme.spacing(8),
      borderBottom: '1px solid rgba(255,255,255,0.3)',
      borderLeft: '1px solid rgba(255,255,255,0.3)',
    },
  },
  withoutContainer: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'flex-start',
    [theme.breakpoints.up('md')]: {
      height: 325,
      order: 1,
    },
  },
  without: {
    padding: `${theme.spacing(4)}px ${theme.spacing(6)}px ${theme.spacing(
      5
    )}px 0px`,
    marginLeft: theme.spacing(8),
    [theme.breakpoints.up('md')]: {
      width: `calc(100% - ${theme.spacing(8)}px)`,
      paddingRight: theme.spacing(8),
      paddingBottom: theme.spacing(8),
      borderBottom: '1px solid rgba(255,255,255,0.3)',
    },
  },
  extraCost: {
    borderTop: '1px solid rgba(255,255,255,0.3)',
    marginLeft: theme.spacing(8),
    marginRight: theme.spacing(8),
    paddingTop: theme.spacing(4),
    [theme.breakpoints.up('md')]: {
      margin: 0,
      paddingLeft: theme.spacing(8),
      paddingTop: theme.spacing(6),
      borderTop: 'initial',
      order: 3,
    },
  },
  extraTime: {
    marginLeft: theme.spacing(8),
    marginRight: theme.spacing(8),
    [theme.breakpoints.up('md')]: {
      margin: 0,
      paddingLeft: theme.spacing(8),
      paddingTop: theme.spacing(6),
      order: 4,
    },
  },
  title: {
    marginLeft: theme.spacing(8),
    marginBottom: 'auto',
    display: 'inline-block',
  },
}))

function Results({ results }) {
  const classes = useStyles()
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('md'))

  const withTotal = Math.round(
    results.new.principal + results.new.interest + results.new.interest_accrued
  )
  const withoutTotal = Math.round(
    results.original.principal + results.original.interest
  )
  const accruedPart = Math.round(
    (results.new.interest_accrued / withTotal) * 100
  )
  const withoutHeight = sizes.defaultChart * (1 - accruedPart / 100)

  return (
    <Paper className={classes.results} elevation={0}>
      {/* DIV: need for animations */}
      <div>
        <Grid container className={classes.container}>
          <Grid item xs={12} md={6} className={classes.withContainer}>
            <Typography
              color="secondary"
              align="left"
              variant="subtitle1"
              className={classes.title}
            >
              Moratóriummal
            </Typography>
            <Box className={cx(classes.chartSection, classes.with)}>
              <Bar
                debt={results.new.principal}
                interest={results.new.interest}
                accrued={results.new.interest_accrued}
              />
              <Box className={classes.details}>
                <Box mb={3}>
                  <Typography color="secondary">Hátralévő futamidő:</Typography>
                  <Typography color="secondary" className={classes.info}>
                    <AnimatedNumber val={results.new.remaining_term} /> hónap
                  </Typography>
                </Box>
                <Box mb={3}>
                  {/* TODO:CHECK */}
                  <Typography color="secondary">Teljes kifizetés:</Typography>
                  <Typography color="secondary" className={classes.info}>
                    <AnimatedNumber val={withTotal} /> Ft
                  </Typography>
                </Box>
                {!matches && (
                  <>
                    <Box mb={3}>
                      <Typography color="secondary" className={classes.accrued}>
                        Moratórium miatt felhalmozott kamat:
                      </Typography>
                      <Typography color="secondary" className={classes.info}>
                        <AnimatedNumber
                          val={Math.round(results.new.interest_accrued)}
                        />{' '}
                        Ft
                      </Typography>
                    </Box>
                    <Box mb={3}>
                      <Typography
                        color="secondary"
                        className={classes.interest}
                      >
                        Kamatfizetés:
                      </Typography>
                      <Typography color="secondary" className={classes.info}>
                        <AnimatedNumber
                          val={Math.round(results.new.interest)}
                        />{' '}
                        Ft
                      </Typography>
                    </Box>
                    <Box>
                      <Typography color="secondary" className={classes.debt}>
                        Tőketörlesztés:
                      </Typography>
                      <Typography color="secondary" className={classes.info}>
                        <AnimatedNumber
                          val={Math.round(results.new.principal)}
                        />{' '}
                        Ft
                      </Typography>
                    </Box>
                  </>
                )}
              </Box>
            </Box>
          </Grid>
          <Grid item xs={12} md={6} className={classes.withoutContainer}>
            <Typography
              color="secondary"
              align="left"
              variant="subtitle1"
              className={classes.title}
            >
              Moratórium nélkül
            </Typography>
            <Box className={cx(classes.chartSection, classes.without)}>
              <Bar
                debt={results.original.principal}
                interest={results.original.interest}
                height={withoutHeight}
              />
              <Box className={classes.details}>
                <Box mb={3}>
                  <Typography color="secondary">Hátralévő futamidő:</Typography>
                  <Typography color="secondary" className={classes.info}>
                    <AnimatedNumber val={results.original.remaining_term} />{' '}
                    hónap
                  </Typography>
                </Box>
                <Box mb={3}>
                  {/* TODO: Check */}
                  <Typography color="secondary">Teljes kifizetés:</Typography>
                  <Typography color="secondary" className={classes.info}>
                    <AnimatedNumber val={withoutTotal} /> Ft
                  </Typography>
                </Box>
                {!matches && (
                  <>
                    <Box mb={3}>
                      <Typography
                        color="secondary"
                        className={classes.interest}
                      >
                        Kamatfizetés:
                      </Typography>
                      <Typography color="secondary" className={classes.info}>
                        <AnimatedNumber
                          val={Math.round(results.original.interest)}
                        />{' '}
                        Ft
                      </Typography>
                    </Box>
                    <Box>
                      <Typography color="secondary" className={classes.debt}>
                        Tőketörlesztés:
                      </Typography>
                      <Typography color="secondary" className={classes.info}>
                        <AnimatedNumber
                          val={Math.round(results.original.principal)}
                        />{' '}
                        Ft
                      </Typography>
                    </Box>
                  </>
                )}
              </Box>
            </Box>
          </Grid>
          <Grid item xs={12} md={6} className={classes.extraCost}>
            <Box mb={6}>
              <Typography color="secondary" variant="subtitle2">
                Ennyivel fizetsz többet:
              </Typography>
              <Typography color="secondary" className={classes.endResult}>
                <AnimatedNumber val={withTotal - withoutTotal} /> Ft
              </Typography>
            </Box>
          </Grid>
          <Grid item xs={12} md={6} className={classes.extraTime}>
            <Box>
              <Typography color="secondary" variant="subtitle2">
                Ennyivel nő meg a futamidőd:
              </Typography>
              <Typography color="secondary" className={classes.endResult}>
                <AnimatedNumber
                  val={
                    results.new.remaining_term - results.original.remaining_term
                  }
                />{' '}
                hónap
              </Typography>
            </Box>
          </Grid>
        </Grid>
      </div>
    </Paper>
  )
}

export default Results

Results.propTypes = {
  results: shape({
    original: shape({
      principal: number.isRequired,
      interest: number.isRequired,
      interest_accrued: number.isRequired,
      last_payment_date: string.isRequired,
      remaining_term: number.isRequired,
    }).isRequired,
    new: shape({
      principal: number.isRequired,
      interest: number.isRequired,
      interest_accrued: number.isRequired,
      last_payment_date: string.isRequired,
      remaining_term: number.isRequired,
    }).isRequired,
  }).isRequired,
}
